<!--gjq改过-->
<template>
  <v-app class="login">
    <div class="login-background">
      <v-container style="height: 100%">
        <v-card color="basil" class="login-card">
          <v-card-title class="text-center justify-center py-6">
            <h1 class="font-weight-bold display-2 basil--text">Pet Hospital</h1>
          </v-card-title>

          <v-tabs
            v-model="tab"
            background-color="transparent"
            color="basil"
            grow
          >
            <v-tab
              v-for="(i, item) in items"
              :key="i"
              @click="addbg(item, i)"
              :class="{ active: item != nowIndex }"
            >
              {{ i }}
            </v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab">
            <v-tab-item v-for="item in items" :key="item">
              <v-card color="basil" flat>
                
                <v-card-text v-show="nowIndex === 0">
                  <v-form v-model="valid" ref="loginForm">
                    <v-container>
                      <v-row style="margin-top: -30px">
                        <v-col>
                          <v-text-field
                            v-model="login.username"
                            :rules="nameRules"
                            label="用户名"
                            required
                            name="loginName"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row style="margin-top: -30px">
                        <v-col>
                          <v-text-field
                            v-model="login.password"
                            :rules="pwdRules"
                            label="密码"
                            required
                            type="password"
                            name="loginPwd"
                            clearable
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-card-actions>
                        <v-btn
                          depressed
                          color="primary"
                          @click="handleLogin()"
                          :disabled="!valid"
                        >
                          login
                        </v-btn>
                        <v-btn
                          @click="reset"
                          color="error"
                          class="mr-4 reset-btn"
                        >
                          reset
                        </v-btn>
                      </v-card-actions>
                    </v-container>
                  </v-form>
                </v-card-text>
                <v-card-text v-show="nowIndex === 1">
                  <v-form v-model="valid" ref="regisForm">
                    <v-container>
                      <v-row style="margin-top: -30px">
                        <v-col>
                          <v-text-field
                            v-model="register.username"
                            :rules="nameRules"
                            label="用户名"
                            required
                            name="regisName"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row style="margin-top: -30px">
                        <v-col>
                          <v-text-field
                            v-model="register.password"
                            :rules="pwdRules"
                            label="密码"
                            required
                            type="password"
                            clearable
                            name="regisPwd"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row style="margin-top: -30px">
                        <v-col>
                          <v-text-field
                            v-model="register.checkpwd"
                            :rules="checkRules"
                            label="确认密码"
                            required
                            type="password"
                            clearable
                            name="checkPwd"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-row style="margin-top: -30px">
                        <v-col>
                          <v-text-field
                            v-model="register.email"
                            :rules="emailRules"
                            label="邮箱"
                            required
                            name="email"
                          ></v-text-field>
                        </v-col>
                      </v-row>
                      <v-card-actions>
                        <v-btn
                          depressed
                          color="primary"
                          @click="handleRegister()"
                          :disabled="!valid"
                          >register</v-btn
                        >
                        <v-btn
                          color="error"
                          class="mr-4 reset-btn"
                          @click="regisReset"
                        >
                          reset
                        </v-btn>
                      </v-card-actions>
                    </v-container>
                  </v-form>
                </v-card-text>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
        </v-card>
      </v-container>
    </div>
  </v-app>
</template>


<script>
import axios from "axios";
import qs from "qs";
export default {
  name: "Login",
  data() {
    return {
      tab: null,
      items: ["Login", "Register"],
      valid: true,
      login: {
        username: "",
        password: "",
      },
      register: {
        username: "",
        password: "",
        checkpwd: "",
        email: "",
      },
      nameRules: [
        (v) => !!v || "Name is required",
        (v) =>  v.length <= 20 || "Name should not be longer than 20 characters",
        (v) => v.search(/\s/) === -1 || "please no space",
      ],
      emailRules: [
        (v) => !!v || "email is required",
        (value) => {
          const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
          return pattern.test(value) || "Invalid e-mail.";
        },
      ],
      pwdRules: [
        (v) => !!v || "password is required",
        (v) => v.length >= 6 || "Passwords should be longer than 6 characters",
        (v) =>  v.length <= 20 || "Passwords should not be longer than 20 characters",
        (v) => v.search(/\s/) === -1 || "please no space",
      ],
      checkRules: [
        (v) => this.$data.register.password == v || "password is different",
      ],
      nowIndex: 0,
      active_text: "Login",
    };
  },
  methods: {
    addbg(index, active_text) {
      this.nowIndex = index;
      this.active_text = active_text;
    },
    handleLogin() {
      axios
        .post(
          "/login",
          qs.stringify(this.login)
        )
        .then((res) => {
          console.log("res=>", res);
          if (res.data.code === 200)
            //这里拿到200就算登陆成功
            //这里给拿token留白，之后再写
            this.$router.push("/index_a");
          else
          {
            console.log("错误代码："+res.data.code)
            console.log("错误提示："+res.data.msg)
          }

          // this.$router.push("/index_a");
        })
        .catch((err) => {
          console.log(err);
        });
    },
    handleRegister() {
      axios
        .post("/register", qs.stringify(this.register))
        .then((res) => {
          console.log("res=>", res);
          console.log(res.data.code); //等于-1，登陆失败，等于200，登陆成功
          if (res.data.code === 200)
            //这里拿到200就算注册成功
            //这里给拿token留白，之后再写
            this.$router.push("/index_a");
          else
          {
            console.log("错误代码："+res.data.code)
            console.log("错误提示："+res.data.msg)
          }
          // this.$router.push("/index_a");
        })
        .catch((err) => {
          console.log(err);
        });
      // this.$router.push("/index_a");
    },
    reset() {
      this.$refs.loginForm[0].reset();
    },
    regisReset(){
      this.$refs.regisForm[0].reset();
    }
  },
};
</script>



<style scoped>
.login {
  width: 100%;
  height: 800px;
  /* background-color: antiquewhite; */
}
.login-background {
  height: 100%;
  width: 100%;
  background: linear-gradient(
    144deg,
    rgb(253, 140, 118) 0%,
    rgb(81, 120, 187) 100%
  );
}
.login-card {
  margin: 100px auto;
  width: 450px;
  background-color: bisque;
}

/* Helper classes */
.basil {
  background-color: #fffbe6 !important;
}
.basil--text {
  color: #356859 !important;
}

.reset-btn {
  margin-left: 20px !important;
}
</style>